// src/services/categoryLocalService.js
import { LocalDB } from '@/db';
import { getJWTUser } from '@/utils/jwt';

import { Category } from '@/entities/category-entity';
import { getIDBRepository } from '@mamunmo21/idxlite';

export const categoryLocalService = {
  db: null,
  user: null,
  categoryRepo: null,

  async init() {
    this.db = await LocalDB();
    this.user = await getJWTUser();
    this.categoryRepo = getIDBRepository(this.db, Category);
  },

  async getAll(page = 1, limit = 10) {
    try {
      if (!this.categoryRepo) await this.init();

      const [data, total] = await this.categoryRepo
        .createQueryBuilder('p')
        .relations(["products"])
        .where({ is_deleted: false, user_uuid: this.user.uuid })
        // .where({ name: Like("%Test Category2%") })
        .skip((page - 1) * limit)
        .take(limit)
        .orderBy({ created_at: "DESC" })
        .findAndCount();
      return {
        data,
        total,
        page: Number(page),
        last_page: Math.ceil(total / limit),
      };
    } catch (err) {
      console.error('Failed to fetch categories:', err);
      return { success: false, message: err.message };
    }
  },

  async create(payload) {
    try {
      if (!this.categoryRepo) await this.init();

      if (!payload.created_at) {
        payload.created_at = new Date().toISOString();
      }
      if (!payload.updated_at) {
        payload.updated_at = new Date().toISOString();
      }
      if (!payload.user_uuid) payload.user_uuid = this.user.uuid;
      const saved = await this.categoryRepo.save(payload);
      return { success: true, data: saved };
    } catch (err) {
      console.log('err: ', err);
      return { success: false, message: err.message };
    }
  },

  async update(uuid, payload) {
    try {
      if (!this.categoryRepo) await this.init();

      const cat = await this.categoryRepo.findOneByIndex(uuid);
      if (!cat) return { success: false, message: 'Not found' };

      if (!payload.user_uuid) payload.user_uuid = this.user.uuid;
      Object.assign(cat, payload);

      const updatedProduct = await this.categoryRepo.update(cat.id, cat);
      return { success: true, data: updatedProduct };
    } catch (err) {
      return { success: false, message: err.message };
    }
  },

  async remove(uuid) {
    try {
      if (!this.categoryRepo) await this.init();

      const cat = await this.categoryRepo.findOneByIndex(uuid);
      if (!cat) return { success: false, message: 'Not found' };
      const deletedCategory = await this.categoryRepo.deleteByUuid(uuid);
      return { success: true, data: deletedCategory };
    } catch (err) {
      return { success: false, message: err.message };
    }
  },

  async processQueueSave(payload) {
    try {
      if (!this.categoryRepo) await this.init();

      payload.id = Number(payload.id); // Ensure id is a number
      const existing = await this.categoryRepo.findOne(payload.id);
      if (existing) {
        await this.categoryRepo.update(payload.id, payload);
      } else {
        await this.categoryRepo.save(payload);
      }
    } catch (error) {
      console.error('Error syncing categories:', error);
    }
  }
};
