import { LocalDB } from '@/db';

import { Product } from '@/entities/product-entity';
import { getIDBRepository } from '@mamunmo21/idxlite';

import { globalService } from '@/service/shared/global.service';

export const productSyncService = {
  urlPath: '/product',

  async processQueue(lastSyncTime = null) {
    try {
      const apiProducts = await this.getAllProducts(lastSyncTime);
      const db = await LocalDB();
      const productRepo = getIDBRepository(db, Product);
      // Save or update products in local DB
      for (const cat of (apiProducts ?? [])) {
        cat.id = Number(cat.id); // Ensure id is a number
        const existing = await productRepo.findOne(cat.id);
        if (existing) {
          await productRepo.update(cat.id, cat);
        } else {
          await productRepo.save(cat);
        }
      }
    } catch (error) {
      console.error('Error syncing products:', error);
    }
  },

  async getApiWithPage(page = 1, limit = 200) {
    try {
      const res = await globalService.getPaginatedData(this.urlPath, page, limit);
      return res.data;
    } catch (error) {
      console.error('Error fetching products from API:', error);
      return { data: [] };
    }
  },

  async getAllProducts(lastSyncTime = null) {
    try {
      const res = await globalService.getAll(this.urlPath, lastSyncTime);
      return res.data;
    } catch (error) {
      console.error('Error fetching products from API:', error);
      return { data: [] };
    }
  }
};
