import { Column, CreateDateColumn, PrimaryGeneratedColumn, UpdateDateColumn } from "@mamunmo21/idxlite";


export class BaseEntity {
  @PrimaryGeneratedColumn()
  id; // default value

  @Column({ unique: true, nullable: true, default: () => 'uuid()' })
  uuid;

  @Column({ nullable: true, default: true })
  status;

  @Column({ nullable: true })
  created_by;

  @Column({ nullable: true })
  updated_by;

  @CreateDateColumn({
    type: 'timestamp',
    nullable: true,
    default: () => 'CURRENT_TIMESTAMP',
  })
  created_at;

  @UpdateDateColumn({
    type: 'timestamp',
    nullable: true,
    default: () => 'CURRENT_TIMESTAMP',
    onUpdate: () => Date.now(),
  })
  updated_at;

}