import { LocalDB } from '@/db';

import { Category } from '@/entities/category-entity';

import { getIDBRepository } from '@mamunmo21/idxlite';

import { globalService } from '@/service/shared/global.service';


export const categorySyncService = {
  urlPath: '/category',

  async processQueue(lastSyncTime = null) {
    try {
      const apiCategories = await this.getAllCategory(lastSyncTime);
      const db = await LocalDB();
      const categoryRepo = getIDBRepository(db, Category);

      // Save or update categories in local DB
      for (const cat of (apiCategories ?? [])) {
        cat.id = Number(cat.id); // Ensure id is a number
        const existing = await categoryRepo.findOne(cat.id);
        if (existing) {
          await categoryRepo.update(cat.id, cat);
        } else {
          await categoryRepo.save(cat);
        }
      }
    } catch (error) {
      console.error('Error syncing categories:', error);
      throw error;
    }
  },

  async getApiWithPage(page = 1, limit = 200) {
    try {
      return await globalService.getPaginatedData(this.urlPath, page, limit);
    } catch (error) {
      console.error('Error fetching categories from API:', error);
      return { data: [] };
    }
  },

  async getAllCategory(lastSyncTime = null) {
    try {
      return await globalService.getAll(this.urlPath, lastSyncTime);
    } catch (error) {
      console.error('Error fetching categories from API:', error);
      return { data: [] };
    }
  }
};
