// utils/snackbar.js

import { reactive } from 'vue'

export const snackbar = reactive({
  show: false,
  message: '',
  type: 'success', // 'success' | 'error'
  timeout: 3000,
})

export function setSnackbarMessage(response) {
  snackbar.message = response.data?.message || 'Operation successful'
  snackbar.type = 'success'
  snackbar.show = true
  setTimeout(() => (snackbar.show = false), snackbar.timeout)
}

export function setErrorMessage(error) {
  snackbar.message =
    error.response?.data?.message ||
    error.message ||
    'Something went wrong'
  snackbar.type = 'error'
  snackbar.show = true
  setTimeout(() => (snackbar.show = false), snackbar.timeout)
}
