import { axiosInstance } from '@/stores/axios';

import { Product } from '@/entities/product-entity';

import { Table } from '@/enums/table.enum';
import { OfflineDocType } from '@/enums/offline-doc-type.enum';

import { productLocalService } from './product.local.service';
import { baseLocalService } from '@/service/shared/base-local.service';

export const productApiService = {
  tableType: Table.PRODUCT,

  async getAll(page = 1, limit = 5) {
    const isOnline = navigator.onLine;
    if (!isOnline) {
      //   // Save locally if offline
      return await productLocalService.getAll(page, limit);
    } else {
      const res = await axiosInstance.get('/product', {
        params: {
          page,
          limit
        }
      });
      // if (Array.isArray(res.data.data)) {
      //   for (const cat of res.data.data) {
      //     cat.id = Number(cat.id);
      //     await productLocalService.create(cat);
      //   }
      // }
      return res.data;
    }
  },

  async create(payload) {
    let body = new Product();
    body = { ...body, ...JSON.parse(JSON.stringify(payload)) }
    const isOnline = navigator.onLine; // no need for ref
    if (!isOnline) {
      // Save locally if offline
      const product = await productLocalService.create(body);

      await baseLocalService.save(product?.data, this.tableType, OfflineDocType.ADD);
      return product?.data;
    } else {
      try {
        const res = await axiosInstance.post('/product', body);
        await productLocalService.create(res.data);
        return res.data;
      } catch (error) {
        if (error.code == 'ERR_NETWORK') {
          const product = await productLocalService.create(body);
          await baseLocalService.save(product?.data, this.tableType, OfflineDocType.ADD);
          return product?.data;
        }
      }
    }
  },

  async update(uuid, payload) {
    payload = { ...JSON.parse(JSON.stringify(payload)) }
    const isOnline = navigator.onLine; // no need for ref
    if (!isOnline) {
      // Save locally if offline
      const res = await productLocalService.update(uuid, payload);

      await baseLocalService.save(res.data, this.tableType, OfflineDocType.UPDATE);
      return res.data;
    } else {
      try {
        const res = await axiosInstance.patch(`/product/${uuid}`, payload);
        await productLocalService.update(uuid, payload);
        return res.data;
      } catch (error) {
        if (error.code == 'ERR_NETWORK') {
          const res = await productLocalService.update(uuid, payload);
          await baseLocalService.save(res.data, this.tableType, OfflineDocType.UPDATE);
          return res.data;
        }
      }
    }
  },

  async delete(uuid) {
    const isOnline = navigator.onLine;
    if (!isOnline) {
      // Remove from local if offline
      const deletedItem = await productLocalService.remove(uuid);
      await baseLocalService.save(deletedItem, this.tableType, OfflineDocType.DELETE);
      return deletedItem;
    } else {
      try {
        const res = await axiosInstance.delete(`/product/${uuid}`);
        return res.data;
      } catch (error) {
        if (error.code == 'ERR_NETWORK') {
          const deletedItem = await productLocalService.remove(uuid);
          await baseLocalService.save(deletedItem, this.tableType, OfflineDocType.DELETE);
          return deletedItem;
        }
      }
    }
  },
};
