<template>
  <div id="app">
    <!-- Pass loggedIn to navbar -->
    <AppNav :logged-in="loggedIn" @update-login="setLoginStatus" />
    
    <router-view @login-success="setLoginStatus(true)" />
    <AppFooter />

    <div v-if="updateExists" class="update-toast">
      <p>&#x21bb; New version available.</p>
      <button @click="refreshApp">Update Now</button>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import AppNav from "./layout/NavBar.vue";
import AppFooter from "./layout/Footer.vue";
import PwaUpdateMixin from "./mixins/PwaUpdateMixin";
import SocketMixin from "./mixins/SocketMixin";

export default {
  name: "App",
  components: { AppNav, AppFooter },
  mixins: [PwaUpdateMixin, SocketMixin],
  setup() {
    // ✅ reactive login state
    const loggedIn = ref(localStorage.getItem("auth") === "true");

    const setLoginStatus = (status) => {
      loggedIn.value = status;
      if (status) localStorage.setItem("auth", "true");
      else localStorage.removeItem("auth");
    };

    return { loggedIn, setLoginStatus };
  }
};
</script>

<style>
* { margin:0; padding:0; box-sizing:border-box; }
#app { font-family:Avenir, Helvetica, Arial, sans-serif; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; text-align:center; color:#2c3e50; }
.update-toast {
  position: fixed;
  top: 20px;
  right: 20px;
  background: #ff4d4d;
  color: #fff;
  padding: 1rem 1.5rem;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0,0,0,0.4);
  display: flex;
  align-items: center;
  gap: 1rem;
}
.update-toast button {
  background: #42b883;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
}
</style>
