          </button>
          <button class="menu-item" data-item="messages">
            <div class="menu-icon">💬</div>
            <span class="menu-text">Messages</span>
          </button>
          <router-link to="/category" @click="closeDrawer()" class="menu-item" data-item="analytics">
            <div class="menu-icon">📈</div>
            <span class="menu-text">Category</span>
          </router-link>
          <router-link to="/product" @click="closeDrawer()" class="menu-item" data-item="analytics">
            <div class="menu-icon">📈</div>
            <span class="menu-text">Product</span>
          </router-link>

          <div class="menu-divider"></div>

          <button class="menu-item" data-item="settings">
            <div class="menu-icon">⚙️</div>
            <span class="menu-text">Settings</span>
          </button>
          <button class="menu-item" data-item="help">
            <div class="menu-icon">❓</div>
            <span class="menu-text">Help & Support</span>
          </button>
          <button class="menu-item" data-item="logout">
            <div class="menu-icon">🚪</div>
            <span class="menu-text">Logout</span>
          </button>
        </div>
      </nav>

      <!-- Main App Container -->
      <div class="app-container" :class="{ 'drawer-open': isOpen }">
        <!-- App Bar -->
        <header class="app-bar  mx-auto flex items-center justify-between p-4">
          <button class="menu-button md:hidden ml-3 focus:outline-none" @click="toggleDrawer">☰</button>
          <h1 class="app-title">FSApp</h1>

          <!-- Desktop Menu -->
          <ul class="hidden md:flex space-x-8 text-lg font-semibold">
            <li><router-link to="/home" class="nav-link">Home</router-link></li>
            <li><router-link to="/about" class="nav-link">About</router-link></li>
            <li><router-link to="/category" class="nav-link">Category</router-link></li>
            <li><router-link to="/product" class="nav-link">Product</router-link></li>
            <li><router-link to="/contact" class="nav-link">Contact</router-link></li>
            <li v-if="!loggedIn">
              <router-link to="/login" class="nav-link">Login</router-link>
            </li>
            <li><button @click="logout" class="text-red-600">Logout</button></li>
          </ul>
        </header>
      </div>
    </div>

    <nav class="container mx-auto flex items-center justify-between p-4">

      <h1 class="text-2xl font-bold text-red-600 tracking-wide">SCI</h1>

      <ul class="hidden md:flex space-x-8 text-lg font-semibold">
        <li><router-link to="/home" class="nav-link">Home</router-link></li>
        <li><router-link to="/about" class="nav-link">About</router-link></li>
        <li><router-link to="/category" class="nav-link">Category</router-link></li>
        <li><router-link to="/product" class="nav-link">Product</router-link></li>
        <li><router-link to="/contact" class="nav-link">Contact</router-link></li>
        <li v-if="!loggedIn">
          <router-link to="/login" class="nav-link">Login</router-link>
        </li>
        <li><button @click="logout" class="text-red-600">Logout</button></li>
      </ul>

      <button @click="toggleMenu" class="md:hidden ml-3 focus:outline-none">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-7 w-7 text-gray-800" fill="none" viewBox="0 0 24 24"
          stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16" />
        </svg>
      </button>
    </nav>
    <!-- mobile menu -->
    <!-- Overlay -->
    <transition enter-active-class="transition-opacity duration-300"
      leave-active-class="transition-opacity duration-300">
      <div v-if="isOpen" class="fixed inset-0 bg-gray-700 bg-opacity-20 z-40" @click="isOpen = false"></div>
    </transition>

    <!-- Sidebar -->
    <transition enter-active-class="transition-transform duration-300 ease-out"
      leave-active-class="transition-transform duration-300 ease-in">
      <div v-if="isOpen"
        class="fixed top-0 right-0 w-72 h-full bg-white shadow-xl z-50 overflow-y-auto transform translate-x-0">

        <!-- Close Button -->
        <div class="flex justify-end p-4">
          <v-btn icon variant="text" color="grey-darken-2" @click="isOpen = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>

        <!-- Menu Items -->
        <nav class="px-6 mt-4">
          <ul class="space-y-3 text-base font-small text-left">
            <li>
              <router-link to="/home" class="block py-2 px-2 rounded hover:bg-red-50 hover:text-red-600 transition">
                Home
              </router-link>
            </li>
            <li>
              <router-link to="/about" class="block py-2 px-2 rounded hover:bg-red-50 hover:text-red-600 transition">
                About
              </router-link>
            </li>
            <li>
              <router-link to="/category" class="block py-2 px-2 rounded hover:bg-red-50 hover:text-red-600 transition">
                Category
              </router-link>
            </li>
            <li>
              <router-link to="/contact" class="block py-2 px-2 rounded hover:bg-red-50 hover:text-red-600 transition">
                Contact
              </router-link>
            </li>
            <li v-if="!loggedIn">
              <router-link to="/login" class="block py-2 px-2 rounded hover:bg-red-50 hover:text-red-600 transition">
                Login
              </router-link>
            </li>
            <li v-else>
              <button @click="logout"
                class="block w-full text-left py-2 px-2 rounded text-red-600 hover:bg-red-50 transition">
                Logout
              </button>
            </li>
          </ul>
        </nav>
      </div>
    </transition>

  </header>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { useRouter } from "vue-router";

import useDevice from "../mixins/UseDeviceMixin.js";
const { isMobile } = useDevice();
// Props
const props = defineProps({
  loggedIn: Boolean,
});

// Emits
const emit = defineEmits(["update-login"]);
// State
const isOpen = ref(false);
// Router
const router = useRouter();
// Methods
function toggleMenu() {
  isOpen.value = !isOpen.value;
}

function logout() {
  emit("update-login", false); // notify App.vue
  router.push("/login");
}

// const isOpen = ref(false);
const activeItem = ref("dashboard");
const hoverFeature = ref(null);

function toggleDrawer() {
  isOpen.value ? closeDrawer() : openDrawer();
}

function openDrawer() {
  isOpen.value = true;
  if (window.innerWidth <= 768) {
    document.body.style.overflow = "hidden";
  }
}

function closeDrawer() {
  isOpen.value = false;
  document.body.style.overflow = "";
}

function setActiveItem(name) {
  activeItem.value = name;
  if (window.innerWidth <= 768) {
    closeDrawer();
  }
  console.log(`Selected: ${name}`);
}

// Lifecycle hooks
onMounted(() => {
  document.addEventListener("keydown", handleEscape);
});

onBeforeUnmount(() => {
  document.removeEventListener("keydown", handleEscape);
});

function handleEscape(e) {
  if (e.key === "Escape" && isOpen.value) {
    closeDrawer();
  }
}
</script>

<style scoped>
.nav-link {
  @apply relative pb-2 text-gray-700 transition duration-300;
}

.nav-link::after {
  content: "";
  @apply absolute left-0 bottom-0 w-0 h-[2px] bg-red-600 transition-all duration-300;
}

.nav-link:hover::after {
  @apply w-full;
}
</style>
