import { Entity, Column, ManyToOne, Index, OneToOne } from '@mamunmo21/idxlite';
import { Category } from './category-entity';
import { BaseEntity } from './base/base.entity';
import { Asset } from './asset.entity';

@Entity('products')
export class Product extends BaseEntity {
  @Column({ nullable: true })
  name;

  @Column({ nullable: true })
  @Index({ unique: true })
  slug;

  @Column({ unique: true })
  code; // ex: PS-101

  @Column({ nullable: true })
  serial_no;

  @Column({ nullable: true })
  unit;

  @Column('decimal', { precision: 10, scale: 2, default: 0 })
  purchase_price;

  @Column('decimal', { precision: 10, scale: 2, default: 0 })
  sales_price;

  @Column('decimal', { precision: 10, scale: 2, default: 0 })
  mrp;

  @Column('int', { default: 0 })
  max_stock_alert;

  @Column('decimal', { precision: 10, scale: 2, default: 0 })
  discount;

  @Column('decimal', { precision: 10, scale: 2, default: 0 })
  vat;

  @Column('decimal', { precision: 10, scale: 2, default: 0 })
  wholesale_price;

  @Column('int', { default: 0 })
  wholesale_min_qty;

  @Column('int', { default: 0 })
  initial_stock;

  @Column({ nullable: true })
  batch;

  @Column({ nullable: true })
  size;

  @Column({ type: 'date', nullable: true })
  mfg_date;

  @Column({ type: 'date', nullable: true })
  exp_date;

  @Column({ nullable: true, type: 'text' })
  description;

  @Column({ nullable: true })
  asset;

  @Column({ width: 1, default: true })
  is_active;

  @Column({ width: 1, default: false })
  @Index()
  is_deleted;

  @Index()
  @Column({ nullable: true, type: 'char', length: 36 })
  category_uuid;

  @ManyToOne(() => Category, (category) => category.uuid, { onDelete: 'CASCADE' })
  category;

  @Index()
  @Column({ nullable: true, type: 'char', length: 36 })
  user_uuid;

  // @ManyToOne(() => User, (user) => user.uuid, { onDelete: 'CASCADE' })
  // @JoinColumn({ name: 'user_uuid', referencedColumnName: 'uuid' })
  // user;

  @OneToOne(() => Asset, (asset) => asset.uuid, { onDelete: 'CASCADE' })
  asset;
}
