import { createApp } from 'vue';

import App from './App.vue';
import router from './router/index.js';
import './registerServiceWorker'
// if (!window.process?.electron) {
//     import('./registerServiceWorker');
// }
import './assets/tailwind.css'
import './assets/style.css'

import pinia from './stores/index.js'
import { useChatStore } from "@/stores/socket/chat-store";
import { getJWTToken, getJWTUser } from './utils/jwt';

// Vuetify
import vuetify from './plugins/vuetify'   // <-- added Vuetify plugin

const app = createApp(App);
app.use(router);
app.use(pinia);
app.use(vuetify);                          // <-- register Vuetify
app.mount('#app')

// Auto-load token after reload
const chatStore = useChatStore();
const token = getJWTToken();
const user = getJWTUser();

// Connect socket automatically if token exists
if (token && user) {
    chatStore.connect(token);
    chatStore.joinRoom(user.username, user.uuid);
}
