import { axiosInstance } from '@/stores/axios';

export const globalService = {
  /**
   * Fetches a single page of paginated data from an API endpoint.
   *
   * @param {string} urlPath - The API endpoint path (e.g. "/category").
   * @param {number} [page=1] - The page number to fetch (defaults to 1).
   * @param {number} [limit=20] - Number of items per page (defaults to 20).
   * @returns {Promise<Object>} The response data from the API (usually an object with `data` array).
   * @throws Will return an empty array if API request fails.
   */
  async getPaginatedData(urlPath, page = 1, limit = 20) {
    try {
      const res = await axiosInstance.get(urlPath, {
        params: { page, limit }
      });
      return res.data;
    } catch (error) {
      console.error('Error fetching from API:', error);
      return { data: [] };
    }
  },

  /**
   * Fetches all data from a paginated API by continuously requesting
   * pages until no more results are available.
   *
   * @param {string} urlPath - The API endpoint path (e.g. "/category").
   * @param {number} [limit=200] - Number of items per page (defaults to 200).
   * @returns {Promise<Array>} An array containing all items from the API.
   * @throws Will return an empty array if any API request fails.
   */
  async getAll(urlPath, lastSyncTime = null) {
    let page = 1;
    let limit = 5;
    let allData = [];

    let keepFetching = true;
    while (keepFetching) {
      try {
        const params = { page: page, limit: limit };
        if (lastSyncTime) params.start_date = lastSyncTime;

        const response = await axiosInstance.get(urlPath, { params });
        if (!response?.status) return [];

        const responseData = response?.data?.data || [];
        allData = [...allData, ...responseData];

        page += 1;
        if (responseData.length < limit) keepFetching = false;
      } catch (e) {
        console.log('Error during API request: ', e);
        return [];
      }
    }
    return allData;
  }
};
