import { Entity, Column } from '@mamunmo21/idxlite';

import { BaseEntity } from './base/base.entity';

@Entity("assets")
export class Asset extends BaseEntity {
  static _tableName = 'fileuploads';
  @Column({ nullable: true })
  name;

  @Column({ nullable: true })
  size;

  @Column({ nullable: true })
  type;

  @Column({ nullable: true })
  file;
}
