// import { DB } from "@mamunmo21/idxlite";
import { DB, StorageEngine } from "@mamunmo21/idxlite";

import { Product } from "./entities/product-entity";
import { OfflineDoc } from "./entities/offline-doc.entity";
import { Category } from "./entities/category-entity";
import { User } from "./entities/user-entity";
import { Asset } from "./entities/asset.entity";

export class MyDB extends DB {
  constructor(name = 'SCIAppDB', version = 1) {
    super(name, version);
  }
}

export async function LocalDB(version = 3) {
  return MyDB.create({
    name: 'SCIAppDB11',
    version: version,
    engine: StorageEngine.INDEXEDDB,
    entities: [
      Asset,
      Product,
      Category,
      User,
      OfflineDoc
    ],
  });
}
