import { Entity, Column, OneToMany, OneToOne, Index, ManyToOne } from '@mamunmo21/idxlite';

import { BaseEntity } from './base/base.entity';
import { Product } from './product-entity';
import { User } from './user-entity';
import { Asset } from './asset.entity';

@Entity('categories')
export class Category extends BaseEntity {
  @Column({ nullable: true })
  name;

  @Column({ nullable: true })
  @Index({ unique: true })
  slug;

  @Column({ type: 'longtext', nullable: true })
  description;

  @Column({ nullable: true })
  asset;

  @Column({ width: 1, default: true })
  is_active;

  @Column({ width: 1, default: false })
  @Index()
  is_deleted;

  @OneToMany(() => Product, (product) => product.category_id, {
    cascade: true,
    onDelete: 'CASCADE',
  })
  products;

  @Index()
  @Column({ nullable: true, type: 'char', length: 36 })
  user_uuid;

  @ManyToOne(() => User, (user) => user.uuid, { onDelete: 'CASCADE' })
  user;

  @OneToOne(() => Asset, (asset) => asset.uuid, { onDelete: 'CASCADE' })
  asset;
}
