import { categorySyncService } from "@/service/category/cagegory.sync.service";
import { productSyncService } from "@/service/product/product.sync.service";

import { useChatStore } from "@/stores/socket/chat-store";
import { getJWTToken, getJWTUser } from '../utils/jwt';

import { offlineDataUploadService } from "@/service/shared/offline-data-upload.service";

export default {
	data() {
		return {
			// refresh variables
			refreshing: false,
			updateExists: false,
		}
	},

	created() {
		// Listen for online/offline events
		window.addEventListener("online", this.handleOnline);

		// Listen for our custom event from the SW registration
		document.addEventListener('InitialSync', this.handleInitialSync, { once: true })
		// document.addEventListener('swSyncUploads', this.syncUpload, { once: true })
		document.addEventListener('message', this.chatMessage);
		// document.addEventListener('chatMessage2', (event) => {
		// 	console.log("Received chatMessage2:", event.detail);
		// });
	},

	methods: {
		async chatMessage(event) {
			if (event && event.detail && event.detail.data) {
				console.log('event002: ', event.detail.data);
			}
			// await this.processQueue();
			// console.log("Received PwaUpdateMixin chat message globally:", event.detail);
			//
		},

		async handleInitialSync() {
			try {
				const lastSyncTime = localStorage.getItem('lastSyncTime');
				const now = new Date().toISOString();
				
				// Process pulling data queues
				await categorySyncService.processQueue(lastSyncTime);
				await productSyncService.processQueue(lastSyncTime);
				
				// Update last sync time
				localStorage.setItem('lastSyncTime', now);
			} catch (error) {
				console.error('Error during initial sync handling:', error);
				throw error;
			}
		},

		async syncUpload(event) {
			console.log('event: ', event);
			// await this.processQueue();
		},
		async updateQueue(event) {
			console.log('eventInitialSync: ', event);
			// await this.processQueue();
		},

		async handleOnline() {
			try {
				// Auto-load token after reload
				const chatStore = useChatStore();
				const token = getJWTToken();
				const user = getJWTUser();

				// Connect socket automatically if token exists
				if (token && user) {
					chatStore.connect(token);
					chatStore.joinRoom(user.username, user.uuid);
				}

				// Process pulling data queues
				await this.handleInitialSync();

				// Process offline data uploads
				await offlineDataUploadService.uploadOfflineDocs();
			} catch (error) {
				console.error('Error during sync:', error);
			}
		},
	},
}