import { axiosInstance } from '@/stores/axios';
import { categoryLocalService } from './category.local.service';
import { Category } from '@/entities/category-entity';

import { Table } from '@/enums/table.enum';
import { OfflineDocType } from '@/enums/offline-doc-type.enum';

import { baseLocalService } from '@/service/shared/base-local.service';

export const categoryApiService = {
  tableType: Table.CATEGORY,

  async getAll(page = 1, limit = 5) {
    const isOnline = navigator.onLine;
    if (!isOnline) {
      // Save locally if offline
      return await categoryLocalService.getAll(page, limit);
    } else {
      try {
        const res = await axiosInstance.get('/category', {
          params: {
            page,
            limit
          }
        });
        return res.data;
      } catch (error) {
        if (error.code == 'ERR_NETWORK') {
          return await categoryLocalService.getAll(page, limit);
        }
      }

    }
  },

  async create(payload) {
    let body = new Category();
    body = { ...body, ...JSON.parse(JSON.stringify(payload)) }
    const isOnline = navigator.onLine;
    if (!isOnline) {
      // Save locally if offline
      const cat = await categoryLocalService.create(body);
      await baseLocalService.save(cat?.data, this.tableType, OfflineDocType.ADD);
      return cat?.data;
    } else {
      try {
        const res = await axiosInstance.post('/category', body);
        await categoryLocalService.create(res.data);
        return res.data;
      } catch (error) {
        if (error.code == 'ERR_NETWORK') {
          const cat = await categoryLocalService.create(body);
          await baseLocalService.save(cat?.data, this.tableType, OfflineDocType.ADD);
          return cat?.data;
        }
      }
    }
  },

  async update(uuid, payload) {
    payload = { ...JSON.parse(JSON.stringify(payload)) }
    const isOnline = navigator.onLine;
    if (!isOnline) {
      // Save locally if offline
      const res = await categoryLocalService.update(uuid, payload);
      await baseLocalService.save(res.data, this.tableType, OfflineDocType.UPDATE);
      return res.data;
    } else {
      try {
        const res = await axiosInstance.patch(`/category/${uuid}`, payload);
        await categoryLocalService.update(uuid, payload);
        return res.data;
      } catch (error) {
        if (error.code == 'ERR_NETWORK') {
          const res = await categoryLocalService.update(uuid, payload);
          await baseLocalService.save(res.data, this.tableType, OfflineDocType.UPDATE);
          return res.data;
        }
      }
    }
  },

  async delete(uuid) {
    const isOnline = navigator.onLine;
    if (!isOnline) {
      // Remove from local if offline
      const deletedItem = await categoryLocalService.remove(uuid);
      await baseLocalService.save(deletedItem?.data, this.tableType, OfflineDocType.DELETE);
      return deletedItem?.data;
    } else {
      try {
        const res = await axiosInstance.delete(`/category/${uuid}`);
        return res.data;
      } catch (error) {
        if (error.code == 'ERR_NETWORK') {
          const deletedItem = await categoryLocalService.remove(uuid);
          await baseLocalService.save(deletedItem, this.tableType, OfflineDocType.DELETE);
          return deletedItem?.data;
        }
      }
    }
  },
};
