import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../pages/HomeView.vue'
import { getJWTToken } from '@/utils/jwt';

const base = import.meta.env?.BASE_URL || '/';

const routes = [
  {
    path: '/',
    redirect: '/login'   // 👈 Root goes to login first
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView,
    meta: { requiresAuth: true }
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../pages/AboutPage.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/contact',
    name: 'contact',
    component: () => import('../pages/ContactPage.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/category',
    name: 'category',
    component: () => import('../pages/CategoryPage.vue'),
    meta: { requiresAuth: true }
  },

  {
    path: '/product',
    name: 'product',
    component: () => import('../pages/ProductPage/ProductList.vue'),
    meta: { requiresAuth: true }
  },
    {
    path: '/product/edit/:uuid',
    name: 'ProductEdit',
    component: () => import('../pages/ProductPage/ProductEdit.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/product/create',
    name: 'Product',
    component: () => import('../pages/ProductPage/ProductCreate.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../auth/LoginPage.vue'),
    meta: { requiresGuest: true }
  },
  {
    path: '/signup',
    name: 'signup',
    component: () => import('../auth/SignUpPage.vue'),
    meta: { requiresGuest: true }
  }
]

const router = createRouter({
  history: createWebHistory(base),
  routes
})

// ✅ Navigation Guard
router.beforeEach((to, from, next) => {
  const token = getJWTToken()

  if (to.meta.requiresAuth && !token) {
    next('/login')
  } else if (to.meta.requiresGuest && token) {
    next('/home')
  } else {
    next()
  }
})

export default router
