export default {
	data() {
		return {
			// refresh variables
			refreshing: false,
			registration: null,
			updateExists: false,
		}
	},

	created() {
		// Listen for our custom event from the SW registration
		document.addEventListener('swUpdated', this.updateAvailable, { once: true })
		document.addEventListener('swUploadQueue', this.updateQueue, { once: true })
		document.addEventListener('swSyncUploads', this.syncUpload, { once: true })
		//   console.log(' this.updateAvailable: ',  this.updateAvailable);

		// Prevent multiple refreshes
		// navigator.serviceWorker.addEventListener('controllerchange', () => {
		// 	if (this.refreshing) return
		// 	this.refreshing = true
		// 	// Here the actual reload of the page occurs
		// 	window.location.reload()
		// })
	},

	methods: {
		// Store the SW registration so we can send it a message
		// We use `updateExists` to control whatever alert, toast, dialog, etc we want to use
		// To alert the user there is an update they need to refresh for
		updateAvailable(event) {
			this.registration = event.detail
			this.updateExists = true
			this.refreshing = true
		},
		async syncUpload(event) {
			console.log('event: ', event);
			await this.processQueue();
		},
		async updateQueue(event) {
			console.log('event: ', event);
			await this.processQueue();
		},

		// --- IndexedDB helpers usable inside SW ---
		openDB() {
			return new Promise((resolve, reject) => {
				const req = indexedDB.open('upload-db', 1)
				req.onupgradeneeded = () => {
					const db = req.result
					if (!db.objectStoreNames.contains('queue')) {
						const os = db.createObjectStore('queue', { keyPath: 'id', autoIncrement: true })
						os.createIndex('status', 'status')
					}
				}
				req.onsuccess = () => resolve(req.result)
				req.onerror = () => reject(req.error)
			})
		},

		async getAllQueued() {
			const db = await this.openDB()
			return new Promise((resolve, reject) => {
				const tx = db.transaction('queue', 'readonly')
				const req = tx.objectStore('queue').getAll()
				req.onsuccess = () => resolve(req.result || [])
				req.onerror = () => reject(req.error)
			})
		},

		async removeFromQueue(id) {
			const db = await this.openDB()
			return new Promise((resolve, reject) => {
				const tx = db.transaction('queue', 'readwrite')
				tx.objectStore('queue').delete(id)
				tx.oncomplete = () => resolve(true)
				tx.onerror = () => reject(tx.error)
			})
		},
		// --- Core uploader used by SYNC and manual trigger ---
		async processQueue() {

			const items = await this.getAllQueued()
			for (const item of items) {
				// Attempt upload
				try {
					const form = new FormData()
					// item.file is a Blob stored in IDB
					form.append('file', item.file, item.name)
					// Add meta as needed
					form.append('ts', String(item.ts))
					form.append('type', item.type || '')
					console.log('form: ', form);

					// const res = await fetch('/api/upload', { method: 'POST', body: form })
					// if (!res.ok) throw new Error('upload failed: ' + res.status)

					// On success, remove from queue and notify page
					// await this.removeFromQueue(item.id)
					// const allClients = await clients.matchAll({ includeUncontrolled: true })
					// for (const c of allClients) {
					// 	c.postMessage({ type: 'UPLOAD_DONE', name: item.name })
					// }
				} catch (err) {
					// Stop processing further for now; will retry later
					// (or continue if you want best-effort)
					break
				}
			}
		},
		// Called when the user accepts the update
		refreshApp() {
			// Make sure we only send a 'skip waiting' message if the SW is waiting
			if (this.registration && this.refreshing) {
				this.updateExists = false
				this.refreshing = false
				window.location.reload();
			}
			// if (!this.registration || !this.registration.waiting) return
			// // send message to SW to skip the waiting and activate the new SW
			// this.registration.waiting.postMessage({ type: 'SKIP_WAITING' })
		},
	},
}