import { defineStore } from "pinia";
import { io } from "socket.io-client";

export const useChatStore = defineStore("chat", {
  state: () => ({
    socket: null,
    messages: [],
    connected: false,
    events: {}, // dynamic event handlers
  }),
  actions: {
    connect(token) {
      if (!token || this.socket) return;

      this.socket = io("https://ufind-admin-api-v2.scibd.info", {
        auth: { token },
      });

      this.socket.on("connect", () => {
        this.connected = true;
        console.log("Socket connected:", this.socket.id);
        document.dispatchEvent(new CustomEvent('InitialSync', { detail: { socketId: this.socket.id } }));
      });

      this.socket.on("disconnect", () => {
        this.connected = false;
        console.log("Socket disconnected");
      });

      this.socket.on("message", (data) => {
        // // update reactive messages array
        // if (typeof data === "string") {
        //   this.messages.push({ username: "System", message: data });
        // } else {
        //   this.messages.push({ username: data.username, message: data.message });
        // }
        if (Array.isArray(data.data) && data.data.length > 0) {
          for (const msg of data.data) {
            this._emit(msg.type, msg);
          }
        } else {
          this._emit("message", data);
          document.dispatchEvent(new CustomEvent('message', { detail: data }));
        }
      });
    },

    joinRoom(username, user_uuid) {
      this.socket?.emit("channel", { username, user_uuid });
    },

    leaveRoom(user_uuid) {
      this.socket?.emit("leaveChannel", { user_uuid });
    },

    sendMessage(user_uuid, message) {
      this.socket?.emit("message", { user_uuid, message });
    },

    disconnect() {
      this.socket?.disconnect();
      this.socket = null;
      this.connected = false;
      this.events = {};
    },

    // -----------------------
    // Dynamic Event System
    // -----------------------
    on(eventName, callback) {
      if (!this.events[eventName]) this.events[eventName] = [];
      this.events[eventName].push(callback);
    },

    off(eventName, callback) {
      if (!this.events[eventName]) return;
      this.events[eventName] = this.events[eventName].filter((cb) => cb !== callback);
    },

    _emit(eventName, payload) {
      if (!this.events[eventName]) return;
      this.events[eventName].forEach((cb) => cb(payload));
    },
  },
});
