export const connectionService = {
  isOnline: navigator.onLine, // initial state

  /**
   * Initialize event listeners for online/offline events
   */
  _init() {
    window.addEventListener("online", this.setOnline.bind(this));
    window.addEventListener("offline", this.setOffline.bind(this));
  },

  setOnline() {
    this.isOnline = true;
    console.log("Online");
  },

  setOffline() {
    this.isOnline = false;
    console.log("Offline");
  },
}

connectionService._init();

