// src/services/productLocalService.js
import { LocalDB } from '@/db';
import { Product } from '@/entities/product-entity';
import { getJWTUser } from '@/utils/jwt';

import { getIDBRepository } from '@mamunmo21/idxlite';

export const productLocalService = {
  db: null,
  user: null,
  productRepo: null,

  async init() {
    this.db = await LocalDB();
    this.user = await getJWTUser();
    this.productRepo = getIDBRepository(this.db, Product);
  },

  async getAll(page = 1, limit = 10) {
    try {
      if (!this.productRepo) await this.init();

      const [data, total] = await this.productRepo
        .createQueryBuilder('p')
        .where({ is_deleted: false, user_uuid: this.user.uuid })
        // .where({ name: Like("%Test Product2%") })
        .skip((page - 1) * limit)
        .take(limit)
        .orderBy({ created_at: "DESC" })
        .findAndCount();
      return {
        data,
        total,
        page: Number(page),
        last_page: Math.ceil(total / limit),
      };
    } catch (err) {
      console.error('Failed to fetch categories:', err);
      return { success: false, message: err.message };
    }
  },

  async create(payload) {
    try {
      if (!this.productRepo) await this.init();

      if (!payload.created_at) {
        payload.created_at = new Date().toISOString();
      }
      if (!payload.updated_at) {
        payload.updated_at = new Date().toISOString();
      }
      const saved = await this.productRepo.save(payload);
      return { success: true, data: saved };
    } catch (err) {
      console.log('err: ', err);
      return { success: false, message: err.message };
    }
  },

  async update(uuid, payload) {
    try {
      if (!this.productRepo) await this.init();

      const product = await this.productRepo.getByUuid(uuid);
      if (!product) return { success: false, message: 'Not found' };
      Object.assign(product, payload);
      const updatedProduct = await this.productRepo.update(product.id, product);
      return { success: true, data: updatedProduct };
    } catch (err) {
      return { success: false, message: err.message };
    }
  },

  async remove(uuid) {
    try {
      if (!this.productRepo) await this.init();

      const product = await this.productRepo.getByUuid(uuid);
      if (!product) return { success: false, message: 'Not found' };
      const deletedItem = await this.productRepo.deleteByUuid(uuid);
      return { success: true, message: deletedItem };
    } catch (err) {
      return { success: false, message: err.message };
    }
  },
};
