import axios from 'axios'
import { getJWTToken, getJWTRefreshToken, getTokenExpireTime } from '../utils/jwt'
import { useAuthStore } from '../stores/auth/auth'
import { useGlobalStore } from './global'
import { setErrorMessage, setSnackbarMessage } from '@/utils/snackbar'
// import { environment } from '../environments'

const configAuthorizationHeader = (config) => {
  const token = getJWTToken()
  if (!token) return config
  config.headers.Authorization = `Bearer ${token}`
  return config
}

const onRequest = (config) => {
  const globalStore = useGlobalStore()
  globalStore.loadingStarted?.()
  return configAuthorizationHeader(config)
}

const onRequestError = (error) => {
  const globalStore = useGlobalStore()
  globalStore.loadingFinished?.()
  return Promise.reject(error)
}

const onResponse = (response) => {
  const globalStore = useGlobalStore()
  globalStore.loadingFinished?.()

  if (response.config.method?.toUpperCase() !== 'GET') {
    setSnackbarMessage(response)
  }
  return response
}

const onResponseError = async (error) => {
  const globalStore = useGlobalStore()
  globalStore.loadingFinished?.()

  const authStore = useAuthStore()
  const originalRequest = error.config

  const tokenExpectedExpireTime = getTokenExpireTime()
  const currentTime = new Date().getTime() / 1000
  const tokenExpired = currentTime > tokenExpectedExpireTime

  if (tokenExpired && !originalRequest._retry) {
    originalRequest._retry = true
    const refreshTokenValue = getJWTRefreshToken()

    try {
      const res = await axios.post(
        `https://ufind-admin-api-v2.scibd.info/api/v1/auth/refresh-token`,
        {
          refresh_token: refreshTokenValue
        }
      )

      authStore.setUpdateData(res.data.data)
      originalRequest.headers['Authorization'] = `Bearer ${res.data.data.token}`
      return axios(originalRequest)
    } catch (err) {
      authStore.clearAuthData()
      window.location.href = '/login'
      return Promise.reject(err)
    }
  }

  if ([401, 403].includes(error.response?.status)) {
    if (error.response.data?.translate_code === 'USER.NOT_LOGIN') {
      setErrorMessage(error)
      window.location.href = '/login'
    }
    return
  }

  setErrorMessage(error)
  return Promise.reject(error)
}

export const axiosInstance = axios.create({
  baseURL: `https://ufind-admin-api-v2.scibd.info/api/v1/`,
  headers: {
    'Content-Type': 'application/json',
  },
})

axiosInstance.interceptors.request.use(onRequest, onRequestError)
axiosInstance.interceptors.response.use(onResponse, onResponseError)
