import { LocalDB } from '@/db';
import { getIDBRepository } from '@mamunmo21/idxlite';

import { Table } from "@/enums/table.enum";
import { OfflineDocType } from "@/enums/offline-doc-type.enum";
import { OfflineDoc } from '@/entities/offline-doc.entity';

import { connectionService } from "@/service/shared/connection.service";

import { baseLocalService } from '@/service/shared/base-local.service';
import { productApiService } from "@/service/product/product.api.service";
import { categoryApiService } from "@/service/category/category.api.service";

class OfflineDataUploadService {
  async getOfflineDocs(type) {
    try {
      const db = await LocalDB();
      const offlineDocsRepo = getIDBRepository(db, OfflineDoc);
      const data = await offlineDocsRepo
        .createQueryBuilder("p")
        .where({ type: type })
        .getMany()
      return data;
    } catch (err) {
      console.error('Failed to fetch offline docs:', err);
      return [];
    }
  }

  async uploadOfflineDocs() {
    const addedDocs = await this.getOfflineDocs(OfflineDocType.ADD);
    await this._uploadAddedDocs(addedDocs);

    const updatedDocs = await this.getOfflineDocs(OfflineDocType.UPDATE);
    await this._uploadUpdatedDocs(updatedDocs);

    const deletedDocs = await this.getOfflineDocs(OfflineDocType.DELETE);
    await this._uploadDeletedDocs(deletedDocs);
  }

  async _uploadAddedDocs(docs = []) {
    try {
      for (const doc of docs) {
        if (!connectionService.isOnline) break;
        await this._addedDoc(doc);
        await baseLocalService.remove(doc.uuid);
      }
    } catch (error) {
      console.error('Error uploading added docs:', error);
      throw error;
    }
  }

  async _uploadUpdatedDocs(docs = []) {
    try {
      for (const doc of docs) {
        if (!connectionService.isOnline) break;
        await this._updatedDoc(doc);
        await baseLocalService.remove(doc.uuid);
      }
    } catch (err) {
      console.error("Error in _addedDoc:", err);
      throw err;
    }
  }

  async _uploadDeletedDocs(docs = []) {
    try {
      for (const doc of docs) {
        if (!connectionService.isOnline) break;
        await this._deletedDoc(doc);
        await baseLocalService.remove(doc.uuid);
      }
    } catch (err) {
      console.error("Error in _addedDoc:", err);
      throw err;
    }
  }

  async _addedDoc(data) {
    try {
      const payload = {
        name: data.doc.name,
        description: data.doc.description,
        is_active: data.doc.is_active,
        uuid: data.doc.uuid,
      }

      switch (data.table_type) {
        case Table.PRODUCT:
          return productApiService.create(payload);
        case Table.CATEGORY:
          return categoryApiService.create(payload);
      }
    } catch (err) {
      console.error("Error in _addedDoc:", err);
      throw err;
    }
  }

  async _updatedDoc(data) {
    try {
      const payload = {
        name: data.doc.name,
        description: data.doc.description,
        is_active: data.doc.is_active,
        uuid: data.doc.uuid,
      }

      switch (data.table_type) {
        case Table.PRODUCT:
          return productApiService.update(payload.uuid, payload);
        case Table.CATEGORY:
          return categoryApiService.update(payload.uuid, payload);
      }
    } catch (err) {
      console.error("Error in _addedDoc:", err);
      throw err;
    }
  }

  async _deletedDoc(data) {
    try {
      switch (data.table_type) {
        case Table.PRODUCT:
          return productApiService.delete(data.doc.uuid);
        case Table.CATEGORY:
          return categoryApiService.delete(data.doc.uuid);
      }
    } catch (err) {
      console.error("Error in _addedDoc:", err);
      throw err;
    }
  }
}

export const offlineDataUploadService = new OfflineDataUploadService();
