  async save(data, tableType, operationType) {
    try {
      const db = await LocalDB();
      const offlineDacRepo = getIDBRepository(db, OfflineDoc);

      let body = new OfflineDoc();
      body.table_type = tableType;
      body.type = operationType;
      body.doc = JSON.parse(JSON.stringify(data));

      return await offlineDacRepo.save(body);
    } catch (err) {
      console.error("Offline doc upsert error:", err);
      return { success: false, message: err.message };
    }
  },

  async remove(uuid) {
    try {
      const db = await LocalDB();
      const baseLocalRepo = getIDBRepository(db, OfflineDoc);
      const doc = await baseLocalRepo
        .createQueryBuilder("p")
        .where({ uuid: uuid })
        .getOne();
      if (!doc) return { success: false, message: 'Not found' };
      await baseLocalRepo.delete(doc.id);
      return { success: true, message: 'Deleted' };
    } catch (err) {
      return { success: false, message: err.message };
    }
  },
